.menu-js {
    position: fixed;
    min-height: 778px;
    height: 100%;
    overflow: auto;
    width: 100%;
    z-index: 4;
}

.catalog-wrapper {
    max-width: 1170px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 25px;
    padding-right: 25px;
}

.overflow {
    overflow: hidden;
}

.visibility-hidden {
    padding: 0;
    width: 0;
    height: 0;
    font-size: 0;
    visibility: hidden;
    pointer-events: none;
    margin: 0;
    & > * {
        height: 0;
    }
}

@media (max-width: 1023px) {
    .tablet-hidden {
        padding: 0;
        width: 0;
        height: 0;
        font-size: 0;
        visibility: hidden;
        pointer-events: none;
        margin: 0;
        & > * {
            height: 0;
        }
    }
}